import React from "react"
import { graphql } from "gatsby"
import Img from "gatsby-image"

import SEO from "./../components/seo"
import Layout from "../components/layout"
import "./pakalpojumi.scss"

const ServicesPage = ({ data }) => {
  const services = [
    "COVID-19 Decontamination",
    "Carpet Cleaning",
    "Cleaning Between New Contract Negotiations",
    "New Construction Rough & Final Cleaning",
    "Small & Light Repairs",
    "Strip & Floor Waxing",
    "High-Speed Floor Buffing",
    "Window Washing",
    "Temporary Labor Staff"
  ]

  return (
    <Layout>
      {
        <SEO
          title="Our Services"
          description="We Are A Full Year-Round Building Cleaning Service Group"
        />
      }
      <h1 className="services-title">Our Services:</h1>
      <div className="services-content">
        {data.allFile.edges.map((el, index) => (
          <div className="services-box" key={index}>
            <Img
              className="services-img"
              fluid={el.node.childImageSharp.fluid}
              alt="img"
              backgroundColor="#F4F4F4"
              fadeIn={true}
              durationFadeIn="500"
            />
            <h3 className="services-img-title">{services[index]}</h3>
          </div>
        ))}
      </div>
    </Layout>
  )
}

export const query = graphql`
  {
    allFile(
      filter: { relativePath: {}, relativeDirectory: { eq: "services" } }
      sort: { fields: [base] }
    ) {
      edges {
        node {
          base
          childImageSharp {
            fluid(maxWidth: 600, quality: 100) {
              ...GatsbyImageSharpFluid_withWebp_noBase64
              ...GatsbyImageSharpFluidLimitPresentationSize
            }
          }
        }
      }
    }
  }
`

export default ServicesPage
